"use client";

import { useState, useEffect, FC, lazy, Suspense } from "react";
import { useTranslation } from "../../../i18n";

const Marquee = lazy(() => import("react-fast-marquee"));

const CategoryArea: FC = () => {
  const { t } = useTranslation(['home']);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return null;

  return (
    <div className="catagory-slide" id="catagorySlide">
      <div className="marquee3k" data-speed="1">
        <Suspense fallback={null}>
          <Marquee gradient={false} speed={50}>
             <h2 className="d-flex align-items-center mb-0">
               <span>#</span>
               <span>{t('services_web_design', 'home')}</span>
               <span>#</span>
               <span>{t('services_web_development', 'home')}</span>
               <span>#</span>
               <span>{t('services_ui_ux_design', 'home')}</span>
             </h2>
          </Marquee>
        </Suspense>
      </div>
    </div>
  );
};

export default CategoryArea;
